$prefix: Vertical;

.#{$prefix} {
  width: 100%;
  height: 100%;

  .overSizeName {
    font-size: 56px !important;
    padding: 5px;
    // display: flex;
    // justify-content: center;
  }

  .gg-name {
    text-align: center;
  }

  &-bg {
    z-index: -1;
    position: absolute;
    // width: 100%;
    // height: 100%;
    top: 0;
    left: 0;
  }

  &-header {
    height: 155px;
    padding: 0 30px;
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    margin-bottom: 60px;

    span {
      color: #fff;
      font-size: 48px;
    }
  }

  &-middle-part {

    padding: 0 28px;

    .department-name {
      font-size: 52px;
      color: #fff;
      height: 90px;

      display: flex;
      justify-content: center;
      align-items: center;
    }

    .g-marquee {
      color: red;
      font-size: 52px;
      height: 115px;
      margin-bottom: 15px;
      display: flex;
      align-items: center;
    }

    .main-content {
      display: flex;
      height: 723px;
      margin-bottom: 75px;

      .left-part {
        display: flex;
        flex: 1 1;
        flex-direction: column;
        padding: 0 10px 0 32px;

        .doc-img {
          flex: 1 1;
          display: flex;
          justify-content: center;
          align-items: center;
        }

        .doc-info {
          flex-basis: 29%;
          display: flex;
          justify-content: space-between;
          flex-direction: column;

          .item {
            height: 100px;
            display: flex;

            .label {
              flex: 1 1;
              display: flex;
              justify-content: center;
              align-items: center;
              font-size: 46px;
              color: #fff;
            }

            .content {
              flex-basis: 74%;
              display: flex;
              justify-content: center;
              align-items: center;
              font-size: 56px;
              transition: all, ease-in .3s;
            }
          }
        }
      }

      .right-part {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex-basis: 46.2%;
        padding: 0 42px 0 0px;

        .current {
          height: 295px;
          display: flex;

          .label {
            flex: 1 1;
            color: #fff;
            display: flex;
            flex-direction: column;
            justify-content: center;

            span {
              text-align: center;
              font-size: 62px;
            }
          }

          .content {
            flex-basis: 77.5%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            transition: all, ease .3s;

            span {
              font-size: 80px;
              color: red;  
            }

            span:nth-child(1) {
              font-size: 88px;
            }
          }
        }

        .waiting {
          display: flex;
          height: 382px;

          .label {
            flex: 1 1;
            color: #fff;
            display: flex;
            flex-direction: column;
            justify-content: center;

            span {
              text-align: center;
              font-size: 62px;
            }
          }

          .content {
            flex-basis: 77.5%;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            align-items: center;

            span {
              font-size: 62px;
              color: #000000;
              padding: 5px;
            }
          }
        }
      }
    }


  }

  &-middle-bottom-part {
    padding: 0 28px;
    height: 580px;
    margin-bottom: 55px;

    display: flex;
    justify-content: space-around;
    align-items: center;

    button {
      width: 210px;
      height: 100px;
      font-size: 45px;
    }
  }

  &-bottom-part {
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
      color: #fff;
      font-size: 32px;
    }
  }
}